<template>
    <div class="dapp">
        <div class="top-image">
            <van-image
                    width="100%"
                    height="100%"
                    :src="top_img_src"
                    fit="fill"
            >
                <template v-slot:error>加载失败</template>
                <template v-slot:loading>
                    <van-loading type="spinner" size="20"></van-loading>
                </template>
            </van-image>
        </div>
        <div class="app-box">
            <div class="recommand">
                <strong>推荐</strong>
            </div>
            <div class="apps">
                <div class="one-app-box" v-for="item in app_list" @click="appClick">
                    <div class="icon-box">
                        <van-image
                                width="90%"
                                height="90%"
                                :src="item.icon"
                                fit="fill"
                        >
                            <template v-slot:error>加载失败</template>
                            <template v-slot:loading>
                                <van-loading type="spinner" size="20"></van-loading>
                            </template>
                        </van-image>
                    </div>
                    <div class="app-name">{{item.name}}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { Image, Toast, Loading } from 'vant'
    export default {
        name: 'dapp',
        components:{
            [Image.name]: Image,
            [Loading.name]: Loading,
        },
        data(){
            return {
                top_img_src: 'https://img.yzcdn.cn/vant/cat.jpeg',
                app_list: [
                    {
                        id: 0,
                        name: '生态系统',
                        icon: 'https://img.yzcdn.cn/vant/cat.jpeg',
                    },
                    {
                        id: 1,
                        name: '娱乐系统',
                        icon: 'https://img.yzcdn.cn/vant/cat.jpeg',
                    },
                    {
                        id: 2,
                        name: '商城',
                        icon: 'https://img.yzcdn.cn/vant/cat.jpeg',
                    },
                    {
                        id: 3,
                        name: '交易所',
                        icon: 'https://img.yzcdn.cn/vant/cat.jpeg',
                    },
                    {
                        id: 4,
                        name: '手机充值',
                        icon: 'https://img.yzcdn.cn/vant/cat.jpeg',
                    },
                    {
                        id: 5,
                        name: '游戏',
                        icon: '',
                    }
                ],
            }
        },
        created() {
            this.$parent.active = 2;
        },
        methods: {
            appClick () {
                Toast({
                    // position: 'bottom',
                    message: 'SHT糖果：敬请期待',
                    duration: 1000,
                })
            }
        }
    }
</script>

<style>
    .dapp{
        width: 100vw;
        height: 100vh;
        background: #f5f5f5;
        overflow: auto;
    }
    .top-image{
        width: 95vw;
        height: 40vw;
        border-radius: 2vw;
        overflow: hidden;
        margin: 2.5vw auto;
        background: greenyellow;
    }
    .app-box{
        width: 95vw;
        margin: auto;
    }
    .recommand{
        text-align: left;
        line-height: 10vw;
        width: 100%;
    }
    .apps{
        display: flex;
        width: 100%;
        flex-wrap: wrap;
        justify-content: space-around;
    }
    .one-app-box{
        width: 28vw;
        height: 28vw;
        border-radius: 2vw;
        background: white;
        margin-bottom: 2vw;
    }
    .icon-box{
        height: 78%;
        width: 78%;
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .app-name{
        /*height: 30%;*/
    }
</style>
